* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #e7e5e5;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  font-size: 0;
  line-height: 1.5;
}

body.intro {
  background-image: url("bg-collage.be36cbf8.jpg");
}

body.intro .search-form {
  height: 60px;
  width: 500px;
  transform: translateY(30vh);
}

.search-form {
  width: 350px;
  height: fit-content;
  border: 1px solid #5f5e5e;
  margin: 20px auto;
  transition: transform .3s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.search-form input {
  width: 70%;
  border: 2px solid rgba(0, 0, 0, 0);
  outline: none;
  padding: 10px;
}

.search-btn {
  width: 30%;
  background-color: #bcbcc5;
  border: 2px solid rgba(0, 0, 0, 0);
  padding: 10px;
}

.gallery {
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  grid-auto-rows: 250px;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  padding: 20px;
  list-style: none;
  display: grid;
}

.photo-card:hover {
  transform: scale(1.03);
}

.photo-card {
  height: 100%;
  width: 100%;
  max-width: 400px;
  object-fit: cover;
  transition: transform .3s cubic-bezier(.4, 0, .2, 1);
}

.gallery__image {
  height: 85%;
  width: 100%;
  display: block;
}

.info {
  text-align: center;
  background-color: #fff;
  gap: 5%;
  padding: 1%;
  font-size: 13px;
  display: flex;
}

.info-item {
  width: 20%;
  margin: 0;
}

.info-item b {
  flex-direction: column;
  display: flex;
}

.info-item span {
  font-size: 12px;
  font-weight: 400;
}

